'use client';

import { useEffect, useState } from 'react';
import { getResources } from '@/lib/mock-api';
import { Resource } from '@/lib/types';
import { ResourcesList } from '@/components/dashboard/resources/resources-list';
import { Loader2, Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { AddResourceModal } from '@/components/dashboard/resources/add-resource-modal';

export default function ResourcesPage() {
  const [resources, setResources] = useState<Resource[]>([]);
  const [loading, setLoading] = useState(true);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const loadResources = async () => {
    setLoading(true);
    try {
      const data = await getResources();
      setResources(data);
    } catch (error) {
      console.error('Failed to load resources:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadResources();
  }, []);

  if (loading) {
    return (
      <div className="flex items-center justify-center p-12">
        <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
      </div>
    );
  }

  return (
    <div className="space-y-6 p-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold">Resources</h1>
          <p className="text-sm text-muted-foreground">
            Manage protected network resources and their access policies
          </p>
        </div>
        <Button className="gap-2" onClick={() => setIsModalOpen(true)}>
          <Plus className="h-4 w-4" />
          Add Resource
        </Button>
      </div>

      {/* Resources List */}
      <ResourcesList resources={resources} />

      {/* Add Resource Modal */}
      <AddResourceModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onResourceAdded={loadResources}
      />
    </div>
  );
}
